import React, { Component } from 'react';
import VisibilityIcon from "@material-ui/icons/Visibility";
import resume from './../documents/Web Developer.pdf'
export default  class Resume extends Component {
  constructor(props) {
    super(props)
   // this.downloadResume=this.downloadResume.bind(this)
    this.state={

    }
  }
   
  openNew() {
       window.open(resume, "_blank") //to open new page

  }
    
  render() {
    let resumeData = this.props.resumeData;
    return <section id="resume">
        <div className="row education">
          <div className="three columns header-col">
            <h1>
              <span>Education</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            {resumeData.education && resumeData.education.map(
                (item, index) => {
                  return (
                    <div
                      className="row item"
                      id={`${item.UniversityName}${index}`}
                      key={`${item.UniversityName}${index}`}
                    >
                      <div className="twelve columns">
                        <h3>{item.UniversityName}</h3>
                        <p className="info">
                          {item.specialization}
                          <span>&bull;</span>{" "}
                          <em className="date">
                            {item.MonthOfPassing} {item.YearOfPassing}
                          </em>
                        </p>
                        <p>{item.Achievements}</p>
                      </div>
                    </div>
                  );
                }
              )}
          </div>
        </div>
        <div className="row work">
          <div className="three columns header-col">
            <h1>
              <span>Work</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            {resumeData.work && resumeData.work.map((item, index) => {
                return <div className="row item" id={`${item.CompanyName}${index}`}  key={`${item.CompanyName}${index}`}>
                    <div className="twelve columns">
                      <h3>{item.CompanyName}</h3>
                      <p className="info">
                        {item.specialization}
                        <span>
                          &bull;
                        </span> <em className="date">{item.experience}</em>
                      </p>
                      <p>{item.Achievements}</p>
                    </div>
                  </div>;
              })}
          </div>
        </div>

        <div className="row skill">
          <div className="three columns header-col">
            <h1>
              <span>Skills</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <p>{resumeData.skillsDescription}</p>

            <div className="bars">
              <ul className="skills">
                {resumeData.skills && resumeData.skills.map(
                    (item, index) => {
                      return (
                        <li id={`${item.CompanyName}${index}`} key={`${item.CompanyName}${index}`}>
                          <span
                            className={`bar-expand ${item.skillname.toLowerCase()}`}
                          />
                          <em>{item.skillname}</em>
                        </li>
                      );
                    }
                  )}
              </ul>
            </div>
          </div>
        </div>

        <div className="row skill">
          <div className="three columns header-col">
            <h1>
              <span>Resume</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <button onClick={this.openNew} >
                View <VisibilityIcon />
            </button>
          </div>
        </div>
      </section>;
  }
}