import React, { useState } from "react";
import { Modal, Image , Carousel, Row, Col } from "antd";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import "antd/dist/antd.css";

export default function Portfolio(props) {
  let resumeData = props.resumeData;
  const [poupState, setPoupState] = useState(false);
  const [portfolio, setPortfolio] = useState({});
  const handlePopUp = (item) => {
    setPoupState(true);
    setPortfolio(item);
    console.log("item", item);
  };
  const handleOk = () => {
    setPoupState(false);
  };

  const handleCancel = () => {
    setPoupState(false);
  };
  const renderPopup = () => {
    return (
      <Modal
        title={portfolio?.name}
        visible={poupState}
        onOk={handleOk}
        footer={null}
        onCancel={handleCancel}
        width={1000}
      >
        <Row>
          <Col span={6}>
            {/* <Carousel autoplay dots={{className:'dotsSlider'}} dotPosition={'right'}> */}
              <Image
                src={`${portfolio.imgurl}`}
                alt={`${portfolio.imgurl}`}
                className='item-img'
              />
            {/* </Carousel> */}
          </Col>
          <Col span={18} className='popupDesc'>
            <p>{portfolio?.description}</p>
            { portfolio.web && <>
            <Router>
              Demo Link: <Link to={{pathname: portfolio.web}} target='_blank'>{portfolio.web}</Link>
              </Router>
            </>}
          </Col>
        </Row>
      </Modal>
    );
  };
  return (
    <>
      {poupState && renderPopup()}
      <section id='portfolio'>
        <div className='row'>
          <div className='twelve columns place_center'>
            <h1>Check Out Some of My Works.</h1>
            <div id='portfolio-wrapper' className='carousel s-bgrid-thirds cf'>
              {resumeData.portfolio &&
                resumeData.portfolio.map((item, index) => {
                  return (
                    <div
                      onClick={(e) => handlePopUp(item)}
                      className='columns portfolio-item'
                      id={`${item.name}${index}`}
                      key={`${item.name}${index}`}
                    >
                      <div className='item-wrap'>
                        <div className='div'>
                          <img
                            src={`${item.imgurl}`}
                            alt={`${item.imgurl}`}
                            className='item-img'
                          />
                          <div className='overlay'>
                            <div className='portfolio-item-meta'>
                              <h5>{item.name}</h5>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })}
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
